
/* Перезагрузка стилей */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: #0f172a;
  color: #f1f5f9;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

/* Контейнер калькулятора */
.calculator-container {
  background-color: #1e293b;
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.5);
  width: 100%;
  max-width: 480px;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  transition: 0.3s ease all;
}

/* Заголовок */
.calculator-title {
  font-size: 2rem;
  text-align: center;
  color: #f1f5f9;
  font-weight: 600;
}

/* Метки и поля */
label {
  display: flex;
  flex-direction: column;
  font-size: 0.95rem;
  gap: 0.4rem;
  color: #cbd5e1;
}

.calculator-input,
select {
  padding: 0.65rem 0.75rem;
  background-color: #334155;
  border: 1px solid #475569;
  border-radius: 0.6rem;
  font-size: 1rem;
  color: #f1f5f9;
  outline: none;
  transition: 0.2s ease border;
}

.calculator-input:focus,
select:focus {
  border-color: #3b82f6;
}

/* Кнопка расчета */
.calculate-button {
  background-color: #3b82f6;
  color: white;
  border: none;
  padding: 0.8rem;
  border-radius: 0.6rem;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.calculate-button:hover {
  background-color: #2563eb;
}

/* Результаты */
.result-box {
  background-color: #334155;
  border-radius: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  font-size: 0.95rem;
}

.result-final {
  font-size: 1.25rem;
  font-weight: bold;
  color: #22c55e;
  margin-top: 0.8rem;
  text-align: center;
}

/* Примечание */
.disclaimer {
  font-size: 0.75rem;
  color: #94a3b8;
  text-align: center;
  line-height: 1.4;
  margin-top: 1.5rem;
  border-top: 1px dashed #475569;
  padding-top: 1rem;
}
/* Блок с полями ввода */
.calculator-box {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

/* Дополнительный отступ для кнопки */
.calculate-button {
  margin-top: 0.5rem;
}

/* Кнопка копирования */
.copy-button {
  background-color: #475569;
  color: #f1f5f9;
  border: none;
  padding: 0.6rem;
  border-radius: 0.5rem;
  font-size: 0.9rem;
  margin-top: 1rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.copy-button:hover {
  background-color: #64748b;
}
/* Светлая тема */
.calculator-container.light {
  background-color: #f9fafb;
  color: #0f172a;
}

.calculator-container.light .calculator-title {
  color: #0f172a;
}

.calculator-container.light label {
  color: #1e293b;
}

.calculator-container.light .calculator-input,
.calculator-container.light select {
  background-color: #ffffff;
  border: 1px solid #cbd5e1;
  color: #0f172a;
}

.calculator-container.light .calculator-input:focus,
.calculator-container.light select:focus {
  border-color: #2563eb;
}

.calculator-container.light .result-box {
  background-color: #e2e8f0;
}

.calculator-container.light .result-final {
  color: #16a34a;
}

.calculator-container.light .disclaimer {
  color: #475569;
  border-top: 1px dashed #94a3b8;
}

.calculator-container.light .copy-button {
  background-color: #cbd5e1;
  color: #0f172a;
}

.calculator-container.light .copy-button:hover {
  background-color: #94a3b8;
}

.theme-toggle {
  align-self: flex-end;
  background: none;
  border: none;
  color: inherit;
  cursor: pointer;
  font-size: 0.95rem;
  padding: 0.25rem 0.5rem;
}
body.dark {
  background-color: #0f172a;
  color: #f1f5f9;
}

body.light {
  background-color: #f9fafb;
  color: #0f172a;
}
